@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.main-container{padding:70px 0;background-color:#333}.main-container .title{margin-bottom:50px;text-align:center;font-family:'Great Vibes', cursive;font-size:5rem;color:#eccfb4}.main-container .card-container{display:flex;flex-wrap:wrap;justify-content:center;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}.main-container .card-container .product-card-wrapper{border-radius:40px;background-color:#222;padding:20px;margin:40px;box-shadow:15px 15px 10px rgba(0,0,0,0.1)}.main-container .card-container .product-card-wrapper .border{height:100%;padding:3px;border-radius:28px;background:linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);-webkit-animation:animatedgradient 3s ease alternate infinite;animation:animatedgradient 3s ease alternate infinite}.main-container .card-container .product-card-wrapper .border .product-card{height:100%;width:300px;padding:25px 25px;background-color:#222;color:#fff;border-radius:25px;display:flex;flex-direction:column;justify-content:space-between}.main-container .card-container .product-card-wrapper .border .product-card .img-wrapper{width:100%;height:200px;margin-bottom:20px;border-radius:20px;overflow:hidden;display:flex;justify-content:center;align-items:center}.main-container .card-container .product-card-wrapper .border .product-card .img-wrapper img{height:100%;width:100%;object-fit:contain}.main-container .card-container .product-card-wrapper .border .product-card .product-title{margin-bottom:20px;font-size:1.5rem;text-align:center}.main-container .card-container .product-card-wrapper .border .product-card .product-info{font-family:'Josefin Sans', sans-serif;font-size:1.2rem;font-weight:300;text-align:justify}.main-container .card-container .product-card-wrapper .border .product-card .add-to-cart{margin-top:30px;padding:10px 0 13px 0;background-color:#222;color:#fff;font-size:1rem;font-weight:700;font-family:'Josefin Sans', sans-serif;outline:none;border:2px solid #fff;border-radius:10px}.main-container .card-container .product-card-wrapper .border .product-card .add-to-cart span{font-size:1.3rem}.main-container .card-container .product-card-wrapper .border .product-card .add-to-cart:hover{cursor:pointer;background-color:#eccfb4;border-color:#eccfb4;color:#222;font-weight:700}

